import React, { Component, Fragment } from 'react';
import { Form, Col } from 'antd';
import { getTrainTypes } from 'services/trainType';
import { getFacultys } from 'services/faculty';
import { getCourses } from 'services/course';
import { getStudyFields } from 'services/StudyField';
import { getClasss } from 'services/class';
import { SelectAjax } from '../../index';

export default class FilterPathEducate extends Component {
  state = {
    widthField: 5,
  };

  componentWillMount() {
    const { column } = this.props;
    if (column === 'column') {
      this.setState({ widthField: 24 });
    }
  }

  render() {
    const { optionFieldDecorator = {} } = this.props;
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const { classes } = this.props;
    const trainTypeId = getFieldValue('trainTypeId');
    const facultyId = getFieldValue('facultyId');
    const courseId = getFieldValue('courseId');
    const studyFieldId = getFieldValue('studyFieldId');
    // const classId = getFieldValue('class');
    const { widthField } = this.state;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    let renderFormCommon;
    if (widthField === 24) {
      renderFormCommon = (
        <Fragment>
          <Form.Item {...formItemLayout} label="H">
            {getFieldDecorator('trainTypeId', optionFieldDecorator.trainTypeId)(
              <SelectAjax service={getTrainTypes} />
            )}
          </Form.Item>
          <Form.Item {...formItemLayout} label="Khoa">
            {getFieldDecorator('facultyId', optionFieldDecorator.facultyId)(
              <SelectAjax
                setFieldsValue={setFieldsValue}
                service={getFacultys}
                params={{ trainTypeId }}
                allowGetData={!!trainTypeId}
                // open={!!trainTypeId && !facultyId}
              />
            )}
          </Form.Item>
          <Form.Item {...formItemLayout} label="Kho">
            {getFieldDecorator('courseId', optionFieldDecorator.courseId)(
              <SelectAjax
                setFieldsValue={setFieldsValue}
                service={getCourses}
                params={{ trainTypeId, facultyId }}
                allowGetData={!!facultyId}
                // open={!!facultyId && !courseId}
              />
            )}
          </Form.Item>

          <Form.Item {...formItemLayout} label="CN">
            {getFieldDecorator('studyFieldId', optionFieldDecorator.studyFieldId)(
              <SelectAjax
                service={getStudyFields}
                setFieldsValue={setFieldsValue}
                params={{ trainTypeId, facultyId, courseId }}
                allowGetData={!!courseId}
                // open={!!courseId && !studyFieldId}
              />
            )}
          </Form.Item>

          {!classes ? (
            <Form.Item {...formItemLayout} label="Lp">
              {getFieldDecorator('classId', optionFieldDecorator.classId)(
                <SelectAjax
                  typeValue="int"
                  service={getClasss}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId, facultyId, courseId, studyFieldId }}
                  allowGetData={!!studyFieldId}
                  // open={!!studyFieldId && !classId}
                />
              )}
            </Form.Item>
          ) : null}
        </Fragment>
      );
    } else {
      renderFormCommon = (
        <Fragment>
          <Col xs={24} sm={12} md={widthField}>
            <Form.Item label="H">
              {getFieldDecorator('trainTypeId', optionFieldDecorator.trainTypeId)(
                <SelectAjax service={getTrainTypes} />
              )}
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={widthField}>
            <Form.Item label="Khoa">
              {getFieldDecorator('facultyId', optionFieldDecorator.facultyId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getFacultys}
                  params={{ trainTypeId }}
                  allowGetData={!!trainTypeId}
                  // open={!!trainTypeId && !facultyId}
                />
              )}
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={widthField}>
            <Form.Item label="Kho">
              {getFieldDecorator('courseId', optionFieldDecorator.courseId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getCourses}
                  params={{ trainTypeId, facultyId }}
                  allowGetData={!!facultyId}
                  // open={!!facultyId && !courseId}
                />
              )}
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={widthField}>
            <Form.Item label="CN">
              {getFieldDecorator('studyFieldId', optionFieldDecorator.studyFieldId)(
                <SelectAjax
                  service={getStudyFields}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId, facultyId, courseId }}
                  allowGetData={!!courseId}
                  // open={!!courseId && !studyFieldId}
                />
              )}
            </Form.Item>
          </Col>
          {!classes ? (
            <Col xs={24} sm={12} md={4}>
              <Form.Item label="Lp">
                {getFieldDecorator('classId', optionFieldDecorator.classId)(
                  <SelectAjax
                    typeValue="int"
                    service={getClasss}
                    setFieldsValue={setFieldsValue}
                    params={{ trainTypeId, facultyId, courseId, studyFieldId }}
                    allowGetData={!!studyFieldId}
                    // open={!!studyFieldId && !classId}
                  />
                )}
              </Form.Item>
            </Col>
          ) : null}
        </Fragment>
      );
    }
    return <Fragment>{renderFormCommon}</Fragment>;
  }
}



